import Vue from 'vue'
import Vuex from 'vuex'
import firebase from 'firebase'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoggedIn: false,
    userId:'',
    userData:'',
  },
  mutations: {
    async isLoggedIn(state) {
      let user       = firebase.auth().currentUser;
      let userRecord = await firebase.firestore().collection('users').doc(user.uid).get()

      if(userRecord.exists === false){
        await firebase.firestore().collection('users').doc(user.uid).set({uid:user.uid,email:user.email})
        state.userId    = userRecord.id
        state.userData  = userRecord.data()
      }

      state.isLoggedIn = true
    },
    async isLoggedOut(state) {
      state.isLoggedIn = false
      state.userId    = ''
      state.userData  = {}
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    isLoggedIn: state => state.isLoggedIn,
  }
})
