<template>

  <div class="home" v-on:mousemove="updateMove">

    <div class="loading-wrapper" v-if=isLoading>
      <div class="loading-inner-wrapper">
        <Circle2></Circle2>
      </div>
    </div>
    <div v-else>

      <div><img :src="`${campaignImage}`" class="drawing-image" /></div>

      <div class="drawing-thank-you-wrapper" v-if=isClosed>
        <div class="drawing-thank-you-sub-title"></div>
        <div class="drawing-thank-you-title">DRAWING IS CLOSED</div>
        <div class="drawing-thank-you-link"><a v-bind:href="shopBtnUrl">{{shopBtnText}} ></a></div>
      </div>
      <div v-else>

      </div>

      <div class="drawing-thank-you-wrapper" v-if=isOpening>
        <div class="drawing-thank-you-sub-title"></div>
        <div class="drawing-thank-you-title">DRAWING WILL OPEN {{campaignStart}}</div>
        <div class="drawing-thank-you-link"><a v-bind:href="shopBtnUrl">{{shopBtnText}} ></a></div>
      </div>
      <div v-else>

      </div>

      <div class="drawing-thank-you-wrapper" v-if=isSuccess>
        <div class="drawing-thank-you-sub-title">YOUR ENTRY HAS BEEN RECEIVED</div>
        <div class="drawing-thank-you-title">GOOD LUCK</div>
        <div class="drawing-thank-you-link"><a v-bind:href="shopBtnUrl">{{shopBtnText}} ></a></div>
      </div>

      <div v-if=showRaffle>
        <!-- start -->

        <div class="drawing-date">DRAW ENDS {{campaignEnd}}</div>
        <div class="drawing-title-top">
          {{campaignTitle}}
        </div>

        <div class="drawing-title-wrapper bottom-border-none">
          <div class="drawing-title-left">
            {{ campaignPrice }}
          </div>

          <div class="drawing-title-right">
            <div class="drawing-description" v-html="campaignDescription"></div>
          </div>
        </div>



        <!-- start -->
        <div v-if=isVerified>
        <div class="drawing-module-wrapper bottom-border-none">

          <div class="drawing-module-left">
            SELECT MODEL
          </div>

          <div class="drawing-module-right">

            <div class="draw-model-wrapper">
              <div v-on:click="setModel(model.id,model.model.name)" v-for="model in models" v-bind:key="model.id" :class="[modelActive === model.id ? 'model-item-active' : 'model-item']">
                <img :src="`${model.model.image}`" />
                {{model.model.name}}
              </div>
            </div>

          </div>
        </div>

        <!-- start -->
        <div class="drawing-module-wrapper bottom-border-none">
          <div class="drawing-module-left">
            SELECT LOCATION
          </div>

          <div class="drawing-module-right">

            <div v-if=selectModel>
                SELECT MODEL
            </div>
            <div class="draw-type-wrapper" v-else>

              <div class="location-item-wrapper" v-for="location in locations" v-bind:key="location.id" >
                <div v-on:click="setLocation(location.id,location.location.locationName)" :class="[locationActive === location.id ? 'location-item-active' : 'location-item']">
                  <div><img :src="`${location.location.locationImage}`" /></div>
                  {{location.location.locationName}}
                </div>
              </div>

            </div>

          </div>
        </div>

        <!-- start -->
        <div class="drawing-module-wrapper bottom-border-none">
          <div class="drawing-module-left">
            SELECT SIZE
          </div>

          <div class="drawing-module-right">

            <div v-if=selectLocation>
                SELECT LOCATION
            </div>
            <div class="size-wrapper" v-else>
              <div v-on:click="setSize(size,i)" v-for="(size, i) in sizes" v-bind:key="size.id" :class="[sizeActive === i ? 'size-item-active' : 'size-item']">
                {{size.size}}
              </div>
            </div>

          </div>
        </div>

        <!-- start -->
        <div class="drawing-module-wrapper bottom-border-none"  v-if=showCustomerMessage>
          <div class="drawing-module-left">
            Enter a message
          </div>

          <div class="drawing-module-right">

            <div class="drawing-form-input">
              <textarea v-model="customerMessage" class="textarea" placeholder="Enter your message here"></textarea>
            </div>

          </div>
        </div>

        <!-- start -->
        <div class="drawing-module-wrapper bottom-border-none">
          <div class="drawing-module-left">
            ENTER YOUR INFO
          </div>

          <div class="drawing-module-right">
            <div class="drawing-form-wrapper">
              <div class="drafwing-form-left">

                <div class="drawing-form-input">
                  <p>Email</p>
                  <input type="text" v-model="campaignDrawEmail" />
                </div>

                <div class="drawing-form-input">
                  <p>First Name</p>
                  <input type="text" v-model="campaignDrawFirstName" />
                </div>


              </div>
              <div class="drafwing-form-right">

                <div class="drawing-form-input">
                  <p>Postal Code</p>
                  <input v-model="campaignDrawZipCode" id="zip" name="zip" type="text" />
                </div>

                <div class="drawing-form-input">
                  <p>Last Name</p>
                  <input type="text" v-model="campaignDrawLastName" />
                </div>

              </div>
            </div>

            <div class="drawing-submit-wrapper">

              <div>
                <input type="checkbox" v-model="campaignDrawAgreeToTerms" /> I agree to the <a href="/pages/terms-and-conditions" target="_blank">terms &amp; conditions</a>
              </div>

              <div>

                <transition name="fade">
                  <div class="drawing-error-wrapper" v-if=hasErrors>
                    <div class="drawing-error-message" v-html="errorMessage"></div>
                  </div>
                </transition>

                <div class="drawing-enter-btn" @click="register">SUBMIT</div>
                <div class="drawing-disclaimer" v-html="terms"></div>
              </div>
            </div>

          </div>
        </div>
        </div>
        <div class="captcha-wrapper" v-else>
          <vue-hcaptcha @verify="onVerify" sitekey="5d390af4-7556-44d7-b77d-2a4ade3ee3b2"></vue-hcaptcha>
        </div>
        <!-- end -->

      </div> <!-- end form wrapper -->
    </div>

  </div>
</template>



<script>
//import { DateTime } from "luxon";
import firebase from 'firebase'
import moment from 'moment'
//import moment from 'moment-timezone'

import {Circle2} from 'vue-loading-spinner'
import VueHcaptcha from '@hcaptcha/vue-hcaptcha'


export default {
  name: 'home',
  components: {
    Circle2,
    VueHcaptcha
  },
  data(){
    return {
      id:'BuR5FveVQ3a7QsHOmB2d', // test campaign id
      customerId:'28202288087691', // test account
      currentDate:'2020-09-02T18:27:00.000-05:00', // test date
      isLoading:true,
      loadingMessage:'Loading Shipping Settings',
      campaignTitle:'',
      campaignDate:'',
      campaignImage:'',
      campaignPrice:'',
      campaignStart:'',
      campaignEnd:'',
      campaignDescription:'',
      campaignDrawType:'',
      campaignDrawSize:'',
      campaignDrawEmail:'',
      campaignDrawFirstName:'',
      campaignDrawLastName:'',
      campaignDrawZipCode:'',
      campaignDrawAgreeToTerms:'',
      campaignModel:'',
      campaignModelName:'',
      campaignLocation:'',
      campaignLocationName:'',
      customerObject:'',
      customerMessage:'not-set',
      countryCode:'',
      countryName:'',
      countryStatus:'',
      captchaToken:'not-set',
      showCustomerMessage:false,
      shopBtnText:'',
      shopBtnUrl:'',
      limitToCountry:'no',
      terms:'',
      models:[],
      locations:[],
      activeLocations:[],
      activeSizes:[],
      hasErrors:false,
      isSuccess:false,
      isClosed:false,
      isOpening:false,
      isVerified:false,
      showRaffle:false,
      hasEntered:false,
      errorMessage:'',
      sizeActive:null,
      modelActive:null,
      locationActive:null,
      selectModel:true,
      selectLocation:true,
      selectSize:true,
      mouseMoved:false,
      drawInStoreActive:'',
      drawOnlineActive:'',
      site:'',
      sizes:[]
    }
  },
  methods:{
    async onVerify(data){
      this.captchaToken = data
      this.isVerified = true
    },
    async onExpired(){

    },
    async onError(){

    },
    async updateMove() {
      this.mouseMoved = true
    },
    async register() {

      let customerObjectx    = window.customerObject[0]
      this.hasErrors = false
      let { campaignLocation, campaignModel, campaignDrawSize, campaignDrawAgreeToTerms, campaignDrawEmail, campaignDrawFirstName, campaignDrawLastName, campaignDrawZipCode } = this
      let formOk = 'yes'

      let errorMessage = ''
      // if(campaignDrawZipCode.length !== 5){
      //   formOk = 'no'
      //   errorMessage = errorMessage + '<p>Please enter a valid Zip Code</p>'
      // }

      if(campaignDrawZipCode.length === 0){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please enter a valid Address</p>'

      }

      if( campaignDrawAgreeToTerms !== true){
        formOk = 'no'
        errorMessage = errorMessage + '<p>You must agree to the terms!</p>'

      }

      if(  /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(campaignDrawEmail) === false ){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please enter a valid email</p>'

      }

      if(campaignDrawEmail.includes('+')){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please enter a valid email</p>'

      }

      if(campaignDrawFirstName.length === 0){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please enter your first name</p>'

      }

      if(campaignDrawLastName.length === 0){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please enter your last name</p>'

      }

      if(campaignModel.length === 0){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please choose a Model</p>'

      }

      if(campaignLocation.length === 0){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please choose a Location</p>'

      }

      if(campaignDrawSize.length === 0){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Please choose a Size</p>'

      }

      if(this.limitToCountry !== 'no'){
        if(customerObjectx.country_code !== this.limitToCountry){
          formOk = 'no'
          errorMessage = errorMessage + `<p>This Raffle is for ${this.limitToCountry} Residence Only</p>`
        }
      }

      if(this.isVerified === false || this.captchaToken === 'not-set'){
        formOk = 'no'
        errorMessage = errorMessage + '<p>Errors registering, please try again later</p>'

      }

      if( formOk === 'no'){
        this.hasErrors    = true
        this.errorMessage =  errorMessage
        return true
      }else{
        // save to campaign
        let currentDate = moment().format();
        let docId       = `${this.id}-${this.customerId}`
        let customerObject = {
          'currentDate': this.customerObject.currentDate,
          'campaignId': this.customerObject.campaignId,
          'accepts_marketing': this.customerObject.accepts_marketing,
          'addresses': this.customerObject.addresses,
          'addresses_count': this.customerObject.addresses_count,
          'email': this.customerObject.email,
          'first_name': this.customerObject.first_name,
          'has_account': this.customerObject.has_account,
          'id': this.customerObject.id,
          'last_name': this.customerObject.last_name,
          'last_order': this.customerObject.last_order,
          'name': this.customerObject.name,
          'orders_count': this.customerObject.orders_count,
          'phone': this.customerObject.phone,
          'tags': this.customerObject.tags,
          'tax_exempt': this.customerObject.tax_exempt,
          'total_spent': this.customerObject.total_spent,
          'country': customerObjectx.country,
          'country_code': customerObjectx.country_code
        }
        let saveObject  = {
          currentDate:currentDate,
          campaignId:this.id,
          customerId:this.customerId,
          type:this.campaignDrawType,
          size:this.campaignDrawSize,
          model:this.campaignModel,
          modelName:this.campaignModelName,
          location:this.campaignLocation,
          locationName:this.campaignLocationName,
          email:this.campaignDrawEmail,
          firstName:this.campaignDrawFirstName,
          lastName:this.campaignDrawLastName,
          zipCode:this.campaignDrawZipCode,
          customerObject:customerObject,
          processed:false,
          mouseMoved:this.mouseMoved,
          customerMessage:this.customerMessage,
          country:customerObjectx.country,
          countryCode:customerObjectx.country_code,
          site:this.site,
          risk:'null',
          captchaToken: this.captchaToken
        }

        let submissionObject  = {
          currentDate:currentDate,
          campaignId:this.id,
          customerId:this.customerId,
          site:this.site,
        }

        await firebase.firestore().collection('submissions').doc(docId).set(saveObject)
        await firebase.firestore().collection('submission_tracking').doc(docId).set(submissionObject)
        this.isSuccess  = true
        this.showRaffle = false
      }

    },
    async setModel(modelId, modelName) {
      this.campaignModel      = modelId
      this.campaignModelName  = modelName
      this.modelActive        = modelId
      let locations           = []

      for(let model of this.models){
        if(model.id === modelId){

          for(let location of model.locations){
            if(location.sizes.length > 0){
                locations.push(location)
            }
          }
        }
      }

      this.locations   = locations

      // if(locations.length === 1){
      //   for(let location of locations){
      //     this.setLocation(location.id)
      //   }
      // }

      this.selectModel = false
    },
    async setLocation(locationId, locationName) {

      this.campaignLocation       = locationId
      this.campaignLocationName   = locationName
      this.locationActive         = locationId
      let sizes                   = []

      for(let model of this.models){
          for(let location of model.locations){
            if(location.id === locationId){
              for(let size of location.sizes){
                sizes.push(size)
              }
            }
          }
      }


      this.sizes          = sizes
      this.selectLocation = false
    },
    async setSize(size, sizeId) {
      this.campaignDrawSize = size.size
      this.sizeActive       = sizeId
      // this.drawInStoreActive  = true
      // this.drawOnlineActive   = false
    },
    async setInStore() {
      this.campaignDrawType = 'in-store'
      this.drawInStoreActive  = true
      this.drawOnlineActive   = false
    },
    async setOnline() {
      this.campaignDrawType   = 'online'
      this.drawInStoreActive  = false
      this.drawOnlineActive   = true
    },
  },
  mounted: async function () {

    //var easternTimeOffset = -240; //for dayLight saving, Eastern time become 4 hours behind UTC thats why its offset is -4x60 = -240 minutes. So when Day light is not active the offset will be -300
    // get shopify customer information ========================================
    window.customerObject = window.customerObject || [];
    let customerObject        = {}
    if(window.customerObject.length > 0){
      customerObject = window.customerObject[0]
      this.campaignDrawEmail      = customerObject.email
      this.campaignDrawFirstName  = customerObject.first_name
      this.campaignDrawLastName   = customerObject.last_name
      this.id                     = customerObject.campaignId
      this.customerId             = customerObject.id
      this.currentDate            = customerObject.currentDate
      //this.campaignDrawZipCode    = customerObject.addresses[0].zip
    }
    // get shopify customer information ========================================

    let sizes                   = ['4','4.5','5','5.5','6','6.5','7','7.5','8','8.5','9','9.5','10','10.5','11','11.5','12','13','14','15']
    let docId                   = `${this.id}-${this.customerId}`

    let hasEntered              = await firebase.firestore().collection('submission_tracking').doc(docId).get()
    let campaign                = await firebase.firestore().collection('campaigns').doc(this.id).get()
    let { campaign_name, campaign_image, campaign_price, start_date, end_date, campaign_description, showCustomerMessage, shopBtnText, shopBtnUrl, terms, site, status }  = campaign.data()

      let modelsRef     = await firebase.firestore().collection('campaigns').doc(this.id).collection('models').orderBy("name").get()
      let models        = []

      for(let model of modelsRef.docs){
        let locationsRef  = await firebase.firestore().collection('campaigns').doc(this.id).collection('models').doc(model.id).collection('locations').orderBy("locationName").get()
        let locations     = []
        for(let location of locationsRef.docs){

          let sizesRef  = await firebase.firestore().collection('campaigns').doc(this.id).collection('models').doc(model.id).collection('locations').doc(location.id).collection('sizes').orderBy("sizeOrder").get()
          let sizes     = []
          for(let sizex of sizesRef.docs){
            let {inventory,size,sizeId,sizeName} = sizex.data()
            let data = {
              id:sizex.id,
              inventory:inventory,
              size:size,
              sizeId:sizeId,
              sizeName:sizeName
            }
            sizes.push(data)
          }
          let locationObject = {
            id: location.id,
            location: location.data(),
            sizes:sizes
          }
          locations.push(locationObject)
        }

        let modelObject   = {
          id:model.id,
          model:model.data(),
          locations: locations
        }
        models.push(modelObject)
      }


    this.showRaffle  = false
    if( status === 'closed' ){
      this.isClosed   = true
    }else if(status === 'open'){
      this.showRaffle   = true
      if(hasEntered.exists){
        this.isSuccess  = true
      }
    }else if(status === 'not-open'){
      this.isOpening   = true
    }

    // format for display...
    start_date                  = start_date.toString()
    end_date                    = moment(end_date).format("M/D/YY h:mm A")

    this.models                 = models

    if(models.length === 1){
      this.setModel(models[0].id,models[0].model.name)
    }

    this.campaignTitle          = campaign_name
    this.campaignImage          = campaign_image
    this.campaignPrice          = campaign_price
    this.campaignStart          = start_date
    this.campaignEnd            = end_date
    this.campaignDescription    = campaign_description
    this.customerObject         = customerObject
    this.sizes                  = sizes
    this.showCustomerMessage    = showCustomerMessage
    this.shopBtnText            = shopBtnText
    this.shopBtnUrl             = shopBtnUrl
    this.terms                  = terms
    this.site                   = site

    this.isLoading  = false
  }
}
</script>
